import React, { PropTypes } from 'react'
import { Form, Input,Select, DatePicker, Button,Modal ,Row,Col} from 'antd'
import moment from 'moment';
import PictureWall from '../upload/PictureWall';

const FormItem = Form.Item
const Option = Select.Option
const { RangePicker } = DatePicker;

const formItemLayout = {
  labelCol: {
    span: 6
  },
  wrapperCol: {
    span: 14
  }
}

const Followup = ({
  followUpsproductTypes:{
    flaWebContactList,
    followStatus,
    followType
  },
  Profiledata,
  visible,
  type,
  item = {},
  onOk,
  onCancel,
  form: {
    getFieldDecorator,
    validateFields,
    getFieldsValue
  }
}) => {
  function handleOk () {
    validateFields((errors) => {
      if (errors) {
        return
      }
      const data = {
        ...getFieldsValue(),
        key: item.key
      }
      onOk(data)
    })
  }
  function inputOnBlur(){

  }
  function handleChange(value) {
    console.log(`selected ${value}`);
  }

  function onChange(value, dateString) {
    console.log('Selected Time: ', value);
    console.log('Formatted Selected Time: ', dateString);
  }

  function onOk(value) {
    console.log('onOk: ', value);
  }
  const modalOpts = {
    title: '',
    visible,
    onOk: handleOk,
    onCancel,
    wrapClassName: 'vertical-center-modal'
  }
  const flaWebContactLisInfo = [];
  flaWebContactList&&flaWebContactList.map((item)=> {
    return (
      flaWebContactLisInfo.push(<Option key={item.id} value={item.id.toString()}>{item.contactName}</Option>)
    )
  })
  const followStatusInfo = [];
  followStatus&&followStatus.map((item)=> {
    return (
      followStatusInfo.push(<Option key={item.valueCode} value={item.valueCode.toString()}>{item.typeName}</Option>)
    )
  })
  const followTypeInfo = [];
  followType&&followType.map((item)=> {
    return (
      followTypeInfo.push(<Option key={item.valueCode} value={item.valueCode.toString()}>{item.typeName}</Option>)
    )
  })
  return (
    <Modal {...modalOpts}footer={[
      <Button key="cancel" size="default" onClick={onCancel}></Button>,
      <Button key="submit" type="primary" size="default" onClick={handleOk}></Button>,
      <Button key="submitAndAdd" type="primary" size="default" onClick={handleOk}></Button>
    ]}>
      <Form horizontal>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('followType', {
            initialValue: item.followType,
            rules: [
              {
                required: true,
                message: ''
              }
            ]
          })( <Select
            showSearch
            style={{ width: 200 }}
            placeholder="Select a person"
            optionFilterProp="children"
            onChange={handleChange}
            filterOption={(input, option) => option.props.value.toLowerCase().indexOf(input.toLowerCase()) >= 0}
          >
            {followTypeInfo}
          </Select>)}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('followTime', {
            initialValue: item.followTime,
          })(
            <DatePicker
              showTime
              format="YYYY-MM-DD HH:mm:ss"
              placeholder="Select Time"
              onChange={onChange}
              defaultValue={moment(new Date(), "YYYY-MM-DD HH:mm:ss")}
              onOk={onOk}
            />
          )}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('content', {
            initialValue: item.content,
          })(<Input type="textarea" rows={4} placeholder=""/>)}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('followStatus', {
            initialValue: item.followStatus,
          })(<Select
            showSearch
            style={{ width: 200 }}
            placeholder="Select a person"
            optionFilterProp="children"
            onChange={handleChange}
            filterOption={(input, option) => option.props.value.toLowerCase().indexOf(input.toLowerCase()) >= 0}
          >
            {followStatusInfo}
          </Select>)}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('contactId', {
            initialValue: item.contactId,
          })(<Select
            showSearch
            style={{ width: 200 }}
            placeholder="Select a person"
            optionFilterProp="children"
            onChange={handleChange}
            filterOption={(input, option) => option.props.value.toLowerCase().indexOf(input.toLowerCase()) >= 0}
          >
            {flaWebContactLisInfo}
          </Select>)}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('nextFollowTime', {
            initialValue: item.nextFollowTime
          })(
            <DatePicker
              showTime
              format="YYYY-MM-DD HH:mm:ss"
              placeholder="Select Time"
              onChange={onChange}
              defaultValue={moment(new Date(), "YYYY-MM-DD HH:mm:ss")}
              onOk={onOk}
            />
          )}
          </FormItem>
        <FormItem label="" hasFeedback {...formItemLayout}>
          {getFieldDecorator('nextFollowContent', {
            initialValue: item.nextFollowContent
          })(
            <Input type="textarea" rows={4} />
          )}
        </FormItem>
      </Form>
    </Modal>
  )
}

Followup.propTypes = {
  visible: PropTypes.any,
  form: PropTypes.object,
  item: PropTypes.object,
  onOk: PropTypes.func,
  onCancel: PropTypes.func
}

export default Form.create()(Followup)



