import React, { PropTypes } from 'react'
import { routerRedux } from 'dva/router'
import { connect } from 'dva'
import CustomerList from '../components/crm/list'
import CustomerSearch from '../components/crm/search'
import CustomerModal from '../components/crm/modal'
import FollowupModal from '../components/crm/followupModal'



function Customers({ location, dispatch, customers,contacts,customersProfile}) {
  const { loading, list, pagination, currentItem, modalVisible, modalType,productTypes ,ModalList,} = customers
  const {ModalVisible,followUpsproductTypes}=customersProfile
  const {ModalVisiblecontact}=contacts
  const { field, keyword } = location.query
  //
  const customerModalProps = {
    item: modalType === 'create' ? {} : currentItem,
    type: modalType,
    productTypes:productTypes,
    visible: modalVisible,
    ModalList:ModalList,
    onOk (data) {
      dispatch({
        type: `customers/${modalType}`,
        payload:{
          data,
          add:false
        } ,
      })
    },
    onOkAdd (data) {
      dispatch({
        type: `customers/${modalType}`,
        payload: {
          data,
          add:true
        }
      })
    },
    onCancel () {
      dispatch({
        type: 'customers/hideModal'
      })
    }
  }
  //
  const customerListProps = {
    dataSources: list,
    loading,
    pagination: pagination,
    onPageChange (page) {
      const { query, pathname } = location
      dispatch(routerRedux.push({
        pathname: pathname,
        query: {
          ...query,
          pageNo: page.current,
          pageSize: page.pageSize
        }
      }))
    },
    onDeleteItem (id) {
      dispatch({
        type: 'customers/delete',
        payload: id
      })
    },
    onEditItem (item) {
      dispatch({
        type: 'customers/editQuery',
        payload: {
          modalType: 'update',
          id: item
        }
      })
    },
    onAddfollowup (item) {
      dispatch({
        type: 'customersProfile/queryFollowUpModal',
        payload: {
          id: item
        }
      })
      dispatch({
        type: 'customersProfile/showModalfollowup',
        payload: {
          modalType: 'create'
        }
      })
    }
  }
  //
  const customerSearchProps = {
    field,
    keyword,
    onSearch (fieldsValue) {
      fieldsValue.keyword.length ? dispatch(routerRedux.push({
        pathname: '/admin/crm',
        query: {
          field: fieldsValue.field,
          keyword: fieldsValue.keyword
        }
      })) : dispatch(routerRedux.push({
        pathname: '/admin/crm'
      }))
    },
    onAdd () {
      dispatch({
        type: 'customers/showModalAdd',
        payload: {
          modalType: 'create'
        }
      })
    }
  }
  ////
  //const contactAddProps = {
  //  item: modalType === 'create' ? {} : currentItem,
  //  type: modalType,
  //  ModalList:ModalList,
  //  productTypes:productTypes,
  //
  //  visible: ModalVisiblecontact,
  //  onOk (data) {
  //    dispatch({
  //      type: `contacts/${modalType}`,
  //      payload: data
  //    })
  //  },
  //  onCancel () {
  //    dispatch({
  //      type: 'contacts/hideModalcontact'
  //    })
  //  }
  //}
  //
  const followupModalProps = {
    item: modalType === 'create' ? {} : currentItem,
    type: modalType,
    visible: ModalVisible,
    followUpsproductTypes:followUpsproductTypes,
    onOk (data) {
      dispatch({
        type: `customersProfile/${modalType}`,
        payload: data
      })
    },
    onOkAdd (data) {
      dispatch({
        type: `customersProfile/${modalType}`,
        payload: data
      })
    },
    onCancel () {
      dispatch({
        type: 'customersProfile/hideModalfollowup'
      })
    }
  }

  const CustomerModalGen = () =>
    <CustomerModal {...customerModalProps} />
  const FollowupModalGen = () =>
    <FollowupModal {...followupModalProps} />
  return (

    <div className='content-inner'>
      <CustomerSearch {...customerSearchProps} />
      <CustomerList {...customerListProps} />
      <CustomerModalGen />
      {followUpsproductTypes?
      <FollowupModalGen/>:''}
    </div>
  )
}

Customers.propTypes = {
  customers: PropTypes.object,
  location: PropTypes.object,
  dispatch: PropTypes.func
}

function mapStateToProps ({ customers,contacts,customersProfile}) {
  return { customers,contacts,customersProfile}
}

export default connect(mapStateToProps)(Customers)



