import React, { PropTypes } from 'react'
import {Row, Col, Form, Input,Select, DatePicker, Button,Modal,Cascader } from 'antd'
import moment from 'moment'
const FormItem = Form.Item
const Option = Select.Option
import 'moment/locale/zh-cn';
moment.locale('zh-cn');

const formItemLayout = {
  labelCol: {
    span: 6
  },
  wrapperCol: {
    span: 14
  }
}

const modal = ({
  visible,
  type,
  item = {},
  onOk,
  onOkAdd,
  onCancel,
  ModalList,
  productTypes:{
    customerTypeList,
    followStatusList,
    sWebProvinceList,
    sourceList,
    scaleList,
    industryList
},
  form: {
    getFieldDecorator,
    validateFields,
    getFieldsValue
  }
}) => {
  function handleOk () {
    validateFields((errors) => {
      if (errors) {
        return
      }
      const data = {
        ...getFieldsValue(),
        key: item.key
      }
      onOk(data)
    })
  }
  function handleOkAdd(){
    validateFields((errors) => {
      if (errors) {
        return
      }
      const data = {
        ...getFieldsValue(),
        key: item.key
      }
      onOkAdd(data)
    })
  }
  const modalOpts = {
    title: `${type === 'create' ? '' : ''}`,
    visible,
    onOk: handleOk,
    onOkAdd:handleOkAdd,
    onCancel,
    wrapClassName: 'vertical-center-modal'
  }
  //
  const customerTypeInfo = [];
  customerTypeList&&customerTypeList.map((item)=> {
    return (
      customerTypeInfo.push(<Option key={item.valueCode} value={item.valueCode.toString()}>{item.valueName}</Option>)
    )
  })
  //
  const followStatusInfo = [];
  followStatusList&&followStatusList.map((item)=> {
    return (
      followStatusInfo.push(<Option key={item.valueCode} value={item.valueCode}>{item.valueName}</Option>)
    )
  })
  //
  const sourceInfo = [];
  sourceList&&sourceList.map((item)=> {
    return (
      sourceInfo.push(<Option key={item.valueCode} value={item.valueCode}>{item.valueName}</Option>)
    )
  })
  //
  const scaleListInfo = [];
  scaleList&&scaleList.map((item)=> {
    return (
      scaleListInfo.push(<Option key={item.valueCode} value ={item.valueCode}>{item.valueName}</Option>)
    )
  })

  //
  const industryListInfo = [];
  industryList&&industryList.map((item)=> {
    return (
      industryListInfo.push(<Option key={item.valueCode} value={item.valueCode}>{item.valueName}</Option>)
    )
  })
  function onChange(value) {
    console.log(value);
  }

  // const nextFollowTime = moment(new Date(ModalList.nextFollow).format('yyyy-MM-dd HH:mm'), 'yyyy-MM-dd HH:mm');
  const nextFollowTime = ModalList?moment(new Date(ModalList.nextFollow)):'';
  return (
    <Modal {...modalOpts} footer={[
      <Button key="cancel" size="default" onClick={onCancel}></Button>,
      <Button key="submit" type="primary" size="default" onClick={handleOk}></Button>,
      <Button key="submitAndAdd" type="primary" size="default" onClick={handleOkAdd}></Button>
    ]}>
      <Form horizontal>
        <FormItem>
          {getFieldDecorator('id', {
            initialValue: ModalList.id
          })(<Input type="hidden"/>)}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('customerName', {
            initialValue: ModalList.customerName||'',
            rules: [
              {
                required: true,
                message: ''
              }
            ]
          })(<Input />)}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('customerType', {
            initialValue: ""+ModalList.customerType+"" ||'',
            rules: [
              {
                required: true,
                message: ''
              }
            ]
          })(
            <Select>
              {customerTypeInfo}
            </Select>
          )}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('tel', {
            initialValue: ModalList.tel||'',
            rules: [
              {
                required: true,
                message: ''
              }
            ]
          })(
            <Input type="Number"/>
          )}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('email', {
            initialValue: ModalList.email||'',
          })(<Input type='Email'/>)}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('fax', {
            initialValue: ModalList.fax||'',
          })(<Input/>)}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('homePage', {
            initialValue: ModalList.homePage||'',
          })(<Input/>)}
        </FormItem>
        <FormItem label='' {...formItemLayout}>
          {getFieldDecorator('region',{
            initialValue: ModalList.region||''
          })(
            <Cascader options={sWebProvinceList} onChange={onChange} placeholder="" />
          )}
        </FormItem>
        <FormItem label='' hasFeedback {...formItemLayout}>
          {getFieldDecorator('address', {
            initialValue: ModalList.address||''
          })(<Input />)}
        </FormItem>
        <FormItem label="" hasFeedback {...formItemLayout}>
          {getFieldDecorator('post', {
            initialValue: ModalList.post||''
          })(<Input type="Number"/>)}
        </FormItem>
        <FormItem label=":" hasFeedback {...formItemLayout}>
          {getFieldDecorator('followStatus',{
            initialValue: ModalList.followStatus||''
          })(
            <Select>
              {followStatusInfo}
            </Select>
          )}
        </FormItem>
        <FormItem label=":" hasFeedback {...formItemLayout}>
          {getFieldDecorator('source',{
            initialValue: ModalList.source||''
          })(
            <Select>
              {sourceInfo}
            </Select>
          )}
        </FormItem>
        <FormItem label="" hasFeedback {...formItemLayout}>
          {getFieldDecorator('industry',{
            initialValue: ModalList.industry||''
          })(
            <Select>
              {industryListInfo}
            </Select>
          )}
        </FormItem>
        <FormItem label="" hasFeedback {...formItemLayout}>
          {getFieldDecorator('scale', {
            initialValue: ModalList.scale|| ''
          })(
            <Select>
              {scaleListInfo}
            </Select>
          )}
        </FormItem>
        <FormItem label=":" hasFeedback {...formItemLayout}>
          {getFieldDecorator('nextFollow',{
            initialValue: nextFollowTime||''
          })(
            <DatePicker
              showTime
              format="YYYY-MM-DD HH:mm"
              placeholder=""
            />
          )}
        </FormItem>
        <FormItem label="" hasFeedback {...formItemLayout}>
          {getFieldDecorator('memo', {
            initialValue: ModalList.memo||''
          })(
            <Input type="textarea" rows={4} />
          )}
        </FormItem>
      </Form>
    </Modal>
  )
}

modal.propTypes = {
  visible: PropTypes.any,
  form: PropTypes.object,
  item: PropTypes.object,
  onOk: PropTypes.func,
  onCancel: PropTypes.func
}

export default Form.create()(modal)



